import axios from 'axios';
import React, { useEffect, useRef } from 'react';
import { Modal } from 'bootstrap';
import Dashboard from '../../components/Admin/Dashboard';
// import ProductsModal from '../../components/Admin/ProductsModal';
import DeleteModal from '../../components/DeleteModal';
import Loading from '../../components/Loading';

function Products() {
  const [state, setState] = React.useState({
    type: 'create',
    data: {},
    loading: true,
  });
  const [data, setData] = React.useState([]);
  const [page, setPage] = React.useState({
    category: '',
    current_page: 1,
    has_next: false,
    has_pre: false,
    total_pages: 1,
  });
  const getData = async (p = 1) => {
    setState((prev) => ({ ...prev, loading: true }));
    const res = await axios(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/orders?page=${p}`);
    setData(res.data.orders);
    setPage(res.data.pagination);
    setState((prev) => ({ ...prev, loading: false }));
  };
  const changePage = (pageNum) => {
    getData(pageNum);
  };

  // const orderModal = useRef('');
  // const orderDetailModal = useRef('');
  const deleteModal = useRef('');
  useEffect(() => {
    getData();
    // orderDetailModal.current = new Modal('#orderDetailModal');
    // orderModal.current = new Modal('#orderModal');
    deleteModal.current = new Modal('#deleteModal');
  }, []);
  // 
  const [deleteData, setDeleteData] = React.useState({
    deleteId: null,
    deleteName: null,
  });
  const handleClose = () => {
    deleteModal.current.hide();
    setDeleteData((preDelete) => ({
      ...preDelete,
      deleteId: null,
      deleteName: null,
    }));
  };
  const openDelete = (id) => {
    deleteModal.current.show();
    setDeleteData((preDelete) => ({
      ...preDelete,
      deleteId: id,
      deleteName: id,
    }));
  };
  const handleDelete = async () => {
    const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/order/${deleteData.deleteId}`, { data: state });
    if (res.data.success) {
      getData();
      deleteModal.current.hide();
    }
  };
  return (
    <Dashboard>
      {
        state.loading && <Loading />
      }
      <DeleteModal
        text={` ${deleteData.deleteName} `}
        close={handleClose}
        check={handleDelete}
      />
      <div className="p-3">
        <h3></h3>
        <hr />
        {/* <ProductsModal type={state.type} data={state.data} refresh={getData} /> */}
        <table className="table">
          <thead>
            <tr>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {
              data.length > 0 && data.map((d) => (
                <tr>
                  <td>{d.id}</td>
                  <td>{d.is_paid ? '' : ''}</td>
                  <td>{new Date(d.paid_date * 1000).toLocaleString('sv-DE')}</td>
                  <td>{d.message}</td>
                  <td>{d.user?.payment}</td>
                  <td>
                    <button className="btn btn-sm btn-outline-primary" type="button"></button>
                  </td>
                  <td>
                    {d.user?.name}
                    {`(${d.user?.email})`}
                  </td>
                  <td>
                    <button
                      type="button"
                      className="btn btn-primary btn-sm"
                      onClick={() => {
                        setState({ type: 'edit', data: d });
                      }}
                    >
                      
                    </button>
                    <button
                      type="button"
                      className="btn btn-outline-danger btn-sm ms-2"
                      onClick={() => { openDelete(d.id); }}
                    >
                      
                    </button>
                  </td>
                </tr>
              ))
            }
          </tbody>
        </table>
        <nav aria-label="Page navigation example">
          <ul className="pagination">
            <li className="page-item">
              <a className={`page-link ${!page.has_pre && 'disabled'}`} href="/" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            {
              [...new Array(page.total_pages)].map((_, i) => (
                <li className="page-item">
                  <a
                    className={`page-link ${(i + 1 === page.current_page) && 'active'}`}
                    href="/"
                    onClick={(e) => {
                      e.preventDefault();
                      changePage(i + 1);
                    }}
                  >
                    {i + 1}

                  </a>

                </li>
              ))
            }
            <li className="page-item">
              <a className={`page-link ${!page.has_next && 'disabled'}`} href="/" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </Dashboard>
  );
}

export default Products;
